import Vue from 'vue';
import VueRouter from 'vue-router';
import CalendarView from '@/components/CalendarView.vue'
import LoginView from '@/components/LoginView.vue'
import MemoView from '@/components/MemoView.vue'
import NavigationView from '@/components/NavigationView.vue'
import AddMemberView from '@/components/AddMemberView.vue'
import UpdateIdView from '@/components/UpdateIdView.vue'
import PracticeView from '@/components/PracticeView.vue'
import UpdatePasswordView from '@/components/UpdatePassView.vue'


Vue.use(VueRouter);

export default new VueRouter({
    mode : 'history',
    base : '/',
    routes : [
        {
            path : '/',
            name : 'LoginView',
            component : LoginView

        },
        {
            path : '/Calendar',
            name : 'CalendarView',
            component : CalendarView

        },
        {
            path : '/Memo',
            name : 'MemoView',
            component : MemoView

        },
        {
            path : '/NavigationView',
            name : 'NavigationView',
            component : NavigationView

        },
        {
            path : '/AddMemberView',
            name : 'AddMemberView',
            component : AddMemberView

        },
        {
            path : '/UpdateIdView',
            name : 'UpdateIdView',
            component : UpdateIdView

        },
        {
            path : '/PracticeView',
            name : 'PracticeView',
            component : PracticeView

        },
        {
            path : '/UpdatePasswordView',
            name : 'UpdatePasswordView',
            component : UpdatePasswordView

        },
]
})