import Vue from 'vue'
import App from './views/HomeView.vue'
import vuetify from './plugins/vuetify'
import VueRouter from 'vue-router';
import 'vuetify/dist/vuetify.min.css'
import router from './router';

Vue.config.productionTip = false

new Vue({
  vuetify,
  VueRouter,
  router,
  render: h => h(App)
}).$mount('#app')
