<template>
    <v-app>
        <v-app-bar-title class="text-center">
            <h1>회원가입 페이지</h1>
        </v-app-bar-title>


        <v-main>
            <v-form>
                <v-container>
                    <v-row>
                    <v-col cols="6">
                        <v-text-field
                                v-model="description"
                                counter
                                maxlength="25"
                                hint="아이디를 입력해주세요"
                                label="아이디"
                                class = "MemberIdText"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                        <v-btn
                                color="blue"
                                dark
                                @click="SnackBar"
                                class="SnackId"
                                >
                                아이디 중복확인
                                </v-btn>
                                <v-snackbar
                                v-model="snackbar"
                                >
                                {{ text }}

                                <template v-slot:action="{ attrs1 }">
                                    <v-btn
                                    color="blue"
                                    text
                                    v-bind="attrs1"
                                    @click="snackbar = false"
                                    >
                                    닫기
                                    </v-btn>
                                </template>
                        </v-snackbar>
                    </v-col>
                </v-row>
                <v-row>
                  <template>
  <div>
    <v-checkbox
      v-model="v0"
      label="비밀번호 안내문구"
      class="text"
    ></v-checkbox>
    <v-banner
    class="text"
      v-model="v0"
      single-line
      transition="slide-y-transition"
    >
    <h4>영문, 숫자, 특수문자를 활용하여 8자 이상으로 작성해주세요.</h4>
    </v-banner>
  </div>
</template>
                </v-row>
                <v-row>
                  <v-col cols="6">
                  <v-text-field
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min]"
                      :type="show1 ? 'text' : 'password'"
                      counter
                      maxlength="25"
                      name="input-10-2"
                      label="비밀번호"
                      hint="조건 만족(8자 이상)"
                      value=""
                      class = "MemberPassText"
                      @click:append="show1 = !show1"
          ></v-text-field>
        </v-col>
                </v-row>
                <v-row>
                  <v-col cols="6">
                  <v-text-field
                      :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min]"
                      :type="show ? 'text' : 'password'"
                      counter
                      maxlength="25"
                      name="input-10-2"
                      label="비밀번호 재확인"
                      hint="조건 만족(8자 이상)"
                      value=""
                      class = "MemberPassText2"
                      @click:append="show = !show"
          ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="6">
                          <v-text-field
                    v-model="email"
                    :rules="emailRules"
                    label="이메일"
                    required
                    class="EmailText"
                  ></v-text-field>
                </v-col>
                <v-col cols="5">
                        <v-btn
                                dark
                                @click="fSnackBarEmail"
                                class="SnackEmail"
                                >
                                인증번호 전송
                                </v-btn>
                                <v-snackbar
                                v-model="snackbarEmail"
                                >
                                {{ EmailText }}

                                <template v-slot:action="{ attrs2 }">
                                    <v-btn
                                    color="blue"
                                    text
                                    v-bind="attrs2"
                                    @click="snackbarEmail = false"
                                    >
                                    닫기
                                    </v-btn>
                                </template>
                        </v-snackbar>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="6">
                        <v-text-field
                                v-model="EmailCheck"
                                counter
                                maxlength="8"
                                hint="인증번호를 입력해주세요"
                                label="이메일 인증번호"
                                class = "EmailCheckText"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="5">
                        <v-btn
                                color="blue"
                                dark
                                @click="SnackBarEmailCheck"
                                class="SnackEmailCheck"
                                >
                                인증번호 확인
                                </v-btn>
                                <v-snackbar
                                v-model="snackbarEmailCheck"
                                >
                                {{ CheckText }}

                                <template v-slot:action="{ attrs3 }">
                                    <v-btn
                                    color="blue"
                                    text
                                    v-bind="attrs3"
                                    @click="snackbarEmailCheck = false"
                                    >
                                    닫기
                                    </v-btn>
                                </template>
                        </v-snackbar>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="5">
                        <v-text-field
                                v-model="Name"
                                counter
                                maxlength="25"
                                hint="이름을 입력해주세요"
                                label="이름"
                                class = "NameText"
                        ></v-text-field>
                    </v-col>
                    <v-col cols="4">
                        <v-text-field
                                v-model="Year"
                                counter
                                maxlength="8"
                                hint="생년월일을 입력해주세요"
                                label="생년월일"
                                class = "YearText"
                        ></v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                  <v-checkbox
                      v-model="boyCheckBox"
                      @change="toggleCheckbox(1)"
                      label="boy"
                      color="indigo"
                      value="boy"
                      hide-details
                      class="boyCheckBox"
            ></v-checkbox>
            <v-checkbox
                      v-model="girlCheckBox"
                      @change="toggleCheckbox(2)"
                      label="girl"
                      color="red"
                      value="girl"
                      hide-details
                      class="girlCheckBox"
            ></v-checkbox>
            <v-btn
                          class="AddMemberBtn"
                          :disabled="dialog"
                          :loading="dialog"
                          color="success"
                          x-large
                          @click="AddMemberbtn"
                        >
                          회원 가입
                        </v-btn>
                        <v-dialog
                        v-model="dialog"
                        hide-overlay
                        persistent
                        width="300"
                      >
                        <v-card
                          color="success"
                          dark
                        >
                          <v-card-text>
                            회원가입 진행중..
                            <v-progress-linear
                              indeterminate
                              color="white"
                              class="mb-0"
                            ></v-progress-linear>
                          </v-card-text>
                        </v-card>
                      </v-dialog>
                </v-row>
                </v-container>
            </v-form>
        </v-main>
    </v-app>



    
</template>

<script>
  export default {
    data: () => ({
      snackbar: false,
      snackbarEmail : false,
      snackbarEmailCheck : false,
      No_text: `중복된 Id입니다.`,
      text: `사용 가능한 Id입니다.`,
      EmailText : `인증번호를 전송하였습니다.`,
      CheckText : `인증 완료됐습니다.`,
      No_CheckText : `잘못된 인증번호입니다.`,
      show : false,
      show1: false,
      loader: null,
      dialog: false,
      password: 'Password',
      v0: true,
      rules: {
          required: value => !!value || '필수 작성(8자 이상)',
          min: v => v.length >= 8 || '최소 8자가 필요합니다.',
          emailMatch: () => (`입력한 비밀번호가 일치하지 않습니다`),
          // 앞서 비밀번호와 동일한 조건값 추가 
        },
      email: '',
      emailRules: [
        v => !!v || 'E-mail을 입력해주세요',
        v => /.+@.+/.test(v) || 'E-mail을 더 작성하세요',
      ],
      boyCheckBox : false,
      girlCheckBox : false
    }),
    methods : {
      toggleCheckbox(checkboxNumber) {
          if (checkboxNumber === 1) {
            this.girlCheckBox = !this.boyCheckBox; // checkbox1이 체크되면 checkbox2는 체크 해제
          } else if (checkboxNumber === 2) {
            this.boyCheckBox = !this.girlCheckBox; // checkbox2가 체크되면 checkbox1은 체크 해제
          }
        },
      AddMemberbtn() {
        this.Loader();
        this.PageDirect();
      },
      Loader(){
        this.dialog = 'dialog'
      },
      PageDirect(){
        setTimeout(() => {
          this.$router.push({name : 'LoginView'})
      }, 3000);
      },
      SnackBar (){
        this.snackbar = true;
        this.snackbarEmail = false;
        this.snackbarEmailCheck = false;
      },
      fSnackBarEmail (){
        this.snackbar = false;
        this.snackbarEmail = true;
        this.snackbarEmailCheck = false;
      }
      ,
      SnackBarEmailCheck (){
        this.snackbar = true;
        this.snackbarEmail = false;
        this.snackbarEmailCheck = true;
      }
    },
    watch: {
      dialog (val) {
        if (!val) return

        setTimeout(() => (this.dialog = false), 3000)
      },
    },
  }
  //주석추가
</script>
 
<style>
.MemberIdText {
  margin-top: 100px;
  margin-left: 150px
}
.SnackId {
  margin-top: 110px;
  margin-left: 55px
}
.text {
  margin-top: 25px;
  margin-left: 155px
}
.MemberPassText {
  margin-top: 0px;
  margin-left: 150px
}
.MemberPassText2 {
  margin-top: 0px;
  margin-left: 150px
}
.EmailText {
  margin-top: 0px;
  margin-left: 150px
} 
.SnackEmail {
  margin-top: 10px;
  margin-left: 60px
}
.EmailCheckText {
  margin-top: 0px;
  margin-left: 150px
}
.SnackEmailCheck {
  margin-top: 10px;
  margin-left: 60px
}
.NameText {
  margin-top: 0px;
  margin-left: 150px
}
.YearText {
  margin-top: 0px;
  margin-left: 50px
}
.boyCheckBox {
  margin-top: 20px;
  margin-left: 160px
}
.girlCheckBox {
  margin-top: 20px;
  margin-left: 30px
}.AddMemberBtn {
  margin-top: 20px;
  margin-left: 235px
}
</style>