<template>
  
    <v-app>
        <v-app-bar-title class="text-center">
            <h1>로그인 페이지</h1>
        </v-app-bar-title>


        <v-main>
    <v-form>
      <v-container>
          <v-row>
          <v-col cols="6">
            <v-text-field
            v-model="description"
            :rules="rules"
            counter
            maxlength="25"
            hint="아이디를 입력해주세요"
            label="아이디"
            class = "IdText"
          ></v-text-field>
            <v-text-field
            :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, rules.min]"
            :type="show3 ? 'text' : 'password'"
            counter
            maxlength="25"
            name="input-10-2"
            label="비밀번호"
            hint="조건 만족(8자 이상)"
            value=""
            class = "PassText"
            @click:append="show3 = !show3"
          ></v-text-field>
        </v-col>
        <v-btn height="150px" width="200px" class="custom-spacer" @click="goCalendar">
          <h1>로그인</h1>
        </v-btn>
          </v-row>
          <v-btn 
          icon
          class="KakaoLogin"
          @click="gokakaoapi">
            <KakaoIconComponent width="50px" height="50px" />
          </v-btn>
          <v-btn 
          icon
          class="GoogleLogin"
          @click="gogoogleapi"
          >
            <GoogleIconComponent width="100px" height="100px" />
          </v-btn>
          <v-row>
            <v-btn
              class="UpdateId"
              :loading="loading4"
              :disabled="loading4"
              style="width: 150px; height: 50px;"
              color="info"
              @click="goUpdateIdView"
            >
              <h2>아이디 찾기</h2> 
              <template v-slot:loader>
                  <v-icon light>mdi-cached</v-icon>
              </template>
       </v-btn>
       <v-btn
              class="UpdatePassword"
              :loading="loading4"
              :disabled="loading4"
              style="width: 150px; height: 50px;"
              color="info"
              @click="goUpdatePasswordView"
            >
              <h2>비밀번호 찾기</h2> 
              <template v-slot:loader>
                  <v-icon light>mdi-cached</v-icon>
              </template>
       </v-btn>
      <v-btn
              class="AddMember"
              :loading="loading4"
              :disabled="loading4"
              style="width: 200px; height: 50px;"
              color="info"
              @click="goMember"
            >
              <h2>회원 가입</h2> 
        </v-btn>
          </v-row>
         
          

      </v-container>
    </v-form>
</v-main>
</v-app>
        

</template>

<script>
import KakaoIconComponent from '@/components/CustomIcon/KakaoIconComponent.vue';
import GoogleIconComponent from '@/components/CustomIcon/GoogleIconComponent.vue';

export default {
  data: () => ({
    show1: false,
    show2: true,
    show3: false,
    show4: false,
    password: 'Password',
    rules: {
          required: value => !!value || '필수 작성(8자 이상)',
          min: v => v.length >= 8 || '최소 8자가 필요합니다.',
          emailMatch: () => (`입력한 비밀번호가 일치하지 않습니다`),
        },
  }),
  components: {
    KakaoIconComponent,
    GoogleIconComponent
  },
  methods : {
    goCalendar () {
        this.$router.push({name : 'CalendarView'})
    },
    gokakaoapi () {
        this.$router.push({name : 'NavigationView'})
    },
    gogoogleapi () {
        this.$router.push({name : 'NavigationView'})
    },
    goMember () {
        this.$router.push({name : 'AddMemberView'})
    },
    goUpdateIdView () {
        this.$router.push({name : 'UpdateIdView'})
    }
    ,
    goUpdatePasswordView () {
        this.$router.push({name : 'UpdatePasswordView'})
    }
  }
}
</script>

