  margin-left: 150px
}
.KakaoLogin {
  margin-top: 50px;
  margin-left: 635px
}
.GoogleLogin {
  margin-top: 50px;
  margin-left: 50px
}
.AddMember {
  margin-top: 80px;
  margin-left: 130px;
}
.UpdateId {
  margin-top: 80px;
